import React from 'react';
import s from './Contacts.module.css';

function Contacts() {
  return (
   <div className={s.contacts}>
       <div className={s.container}>
           <span className={s.blockTitle}>Контакты</span>
           <form className={s.formWrapper}>
               <input type="text" placeholder='Имя' className={s.formArea}/>
               <input type="e-mail" placeholder='E-mail' className={s.formArea}/>
               <textarea placeholder='Сообщение' className={s.messageArea}></textarea>
               <button type='submit' className={s.btnSubmit}>Отправить</button>
           </form>
       </div>
   </div>
  );
}

export default Contacts;
